

"use client";

import { useForm, useFieldArray, Controller } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Input } from "@/components/ui/input";
import { useToast } from "@/hooks/use-toast";
import { useState } from "react";
import { Loader2, PlusCircle, Trash2 } from "lucide-react";
import { useDatabase } from "@/context/database-context";
import { useAuth } from "@/context/auth-context";
import { Semester, Session } from "@/lib/types";
import { ScrollArea } from "./ui/scroll-area";

const sessionFormSchema = z.object({
  teacherName: z.string().min(1, "Please select a teacher."),
  day: z.string().min(1, "Please select a day."),
  sessions: z.array(z.object({
    time: z.string().regex(/^(0?[1-9]|1[0-2]):[0-5][0-9] (AM|PM)$/, "Time must be in HH:MM AM/PM format."),
    duration: z.coerce.number().min(0.5, "Duration must be at least 0.5 hours.").max(8),
    specialization: z.string().min(2, "Specialization is required."),
    type: z.enum(['practical', 'theory']),
  })).min(1, "Please add at least one session."),
});

type SessionFormValues = z.infer<typeof sessionFormSchema>;

interface CreateSessionDialogProps {
  isOpen: boolean;
  onOpenChange: (open: boolean) => void;
  semester: Semester;
  onSessionCreated: () => void;
}

export function CreateSessionDialog({ isOpen, onOpenChange, semester, onSessionCreated }: CreateSessionDialogProps) {
  const { toast } = useToast();
  const { users } = useAuth();
  const { updateSemester } = useDatabase();
  const [isLoading, setIsLoading] = useState(false);

  const form = useForm<SessionFormValues>({
    resolver: zodResolver(sessionFormSchema),
    defaultValues: {
      teacherName: "",
      day: "",
      sessions: [{ time: "10:00 AM", duration: 1, specialization: "Oud", type: "practical" }],
    },
  });

  const { fields, append, remove } = useFieldArray({
    control: form.control,
    name: "sessions"
  });

  const teachers = users.filter(u => u.roles.includes('teacher'));
  const daysOfWeek = ['Saturday', 'Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday'];

  async function onSubmit(data: SessionFormValues) {
    setIsLoading(true);
    const { teacherName, day, sessions } = data;

    const newSessions: Session[] = sessions.map(s => ({
        id: `${day}-${s.time.replace(/[\s:]/g, '')}`,
        time: s.time,
        duration: s.duration,
        specialization: s.specialization,
        type: s.type,
        students: [],
    }));

    const updatedMasterSchedule = { ...semester.masterSchedule };
    if (!updatedMasterSchedule[teacherName]) {
        updatedMasterSchedule[teacherName] = {};
    }
    const existingSessions = updatedMasterSchedule[teacherName][day] || [];
    updatedMasterSchedule[teacherName][day] = [...existingSessions, ...newSessions];

    const success = await updateSemester(semester.id, { masterSchedule: updatedMasterSchedule });

    setIsLoading(false);
    if (success) {
      toast({ title: "Sessions Created", description: `New sessions have been added for ${teacherName} on ${day}.` });
      onSessionCreated();
      onOpenChange(false);
      form.reset();
    }
  }

  return (
    <Dialog open={isOpen} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-lg">
        <DialogHeader>
          <DialogTitle>Create New Sessions</DialogTitle>
          <DialogDescription>
            Add one or more sessions for a teacher on a specific day in the "{semester.name}" semester.
          </DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
            <div className="grid grid-cols-2 gap-4">
                <FormField control={form.control} name="teacherName" render={({ field }) => (
                    <FormItem>
                        <FormLabel>Teacher</FormLabel>
                        <Select onValueChange={field.onChange} defaultValue={field.value}>
                            <FormControl><SelectTrigger><SelectValue placeholder="Select a teacher" /></SelectTrigger></FormControl>
                            <SelectContent>{teachers.map(t => <SelectItem key={t.id} value={t.name}>{t.name}</SelectItem>)}</SelectContent>
                        </Select>
                        <FormMessage />
                    </FormItem>
                )} />
                <FormField control={form.control} name="day" render={({ field }) => (
                    <FormItem>
                        <FormLabel>Day of Week</FormLabel>
                        <Select onValueChange={field.onChange} defaultValue={field.value}>
                            <FormControl><SelectTrigger><SelectValue placeholder="Select a day" /></SelectTrigger></FormControl>
                            <SelectContent>{daysOfWeek.map(d => <SelectItem key={d} value={d}>{d}</SelectItem>)}</SelectContent>
                        </Select>
                        <FormMessage />
                    </FormItem>
                )} />
            </div>

            <ScrollArea className="h-48 border rounded-md p-4">
            <div className="space-y-4">
                {fields.map((field, index) => (
                    <div key={field.id} className="p-2 border rounded-md space-y-2 relative">
                         <div className="grid grid-cols-2 gap-2">
                            <FormField control={form.control} name={`sessions.${index}.time`} render={({ field }) => (
                                <FormItem><FormLabel>Start Time</FormLabel><FormControl><Input placeholder="HH:MM AM/PM" {...field} /></FormControl><FormMessage /></FormItem>
                            )}/>
                            <FormField control={form.control} name={`sessions.${index}.duration`} render={({ field }) => (
                                <FormItem><FormLabel>Duration (hr)</FormLabel><FormControl><Input type="number" step="0.5" {...field} /></FormControl><FormMessage /></FormItem>
                            )}/>
                         </div>
                          <div className="grid grid-cols-2 gap-2">
                            <FormField control={form.control} name={`sessions.${index}.specialization`} render={({ field }) => (
                                <FormItem><FormLabel>Specialization</FormLabel><FormControl><Input placeholder="e.g. Oud" {...field} /></FormControl><FormMessage /></FormItem>
                            )}/>
                             <FormField control={form.control} name={`sessions.${index}.type`} render={({ field }) => (
                                <FormItem><FormLabel>Type</FormLabel>
                                 <Select onValueChange={field.onChange} defaultValue={field.value}>
                                    <FormControl><SelectTrigger><SelectValue/></SelectTrigger></FormControl>
                                    <SelectContent>
                                        <SelectItem value="practical">Practical</SelectItem>
                                        <SelectItem value="theory">Theory</SelectItem>
                                    </SelectContent>
                                </Select>
                                <FormMessage /></FormItem>
                            )}/>
                          </div>
                        <Button type="button" variant="destructive" size="icon" className="absolute top-1 right-1 h-6 w-6" onClick={() => remove(index)}>
                            <Trash2 className="h-4 w-4" />
                        </Button>
                    </div>
                ))}
            </div>
            </ScrollArea>
             <Button type="button" variant="outline" size="sm" onClick={() => append({ time: "10:00 AM", duration: 1, specialization: "Oud", type: 'practical' })}>
                <PlusCircle className="mr-2" /> Add another session
            </Button>
           
            <DialogFooter>
                <Button type="submit" className="w-full" disabled={isLoading}>
                    {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                    Create Sessions
                </Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}


    